import React from "react";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { arachne } from "./arachne.js";
import { Navigator, parseHTML, SearchBox, SelectMenu } from "./elements.js";

class Ressource extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            item: null,
            workLst: []
        };
    }

    render(){
        return (
        <div style={{padding: "0 10px", display: "grid", gridTemplateColumns: "auto 420px", gridTemplateRows: "min-content auto", rowGap: "15px"}}>
            <SearchBox
                searchQuery={(q,order) => {this.searchQuery(q,order)}}
                searchOptions={[
                    ["ac_web", "Werk"],
                    ["work_id", "Werk-ID"],
                    ["ressource", "Ressource"],
                    ["id", "ID"]
                ]}
                sortOptions={[['["id"]', "ID"], ['["label"]', "Kürzel"], ['["opus"]', "vrkn. Werk"]]}
                gridArea={(this.state.item)?"1/1/1/2":"1/1/1/3"}
            />
            <RessourceBox
                loadPage={newPage => {this.loadPage(newPage)}}
                currentElements={this.state.currentElements}
                count={this.state.count}
                currentPage={this.state.currentPage}
                maxPage={this.state.maxPage}
                gridArea={(this.state.item)?"2/1/2/2":"2/1/2/3"}
                showDetail={item => {
                    this.setState({item: item});
                }}
            />
            {(this.state.item)?<RessourceAside workLst={this.state.workLst} status={this.props.status} item={this.state.item} onUpdate={id=>{this.reloadEntry(id)}} />:""}
        </div>
        );
    }
    componentDidMount(){
        const getWorks = async () => {
            const works = await arachne.work.get({in_use: 1}, {select: ["id", "ac_web"], order: ["ac_web"]});
            let newWorkLst = [];
            for(const work of works){
                newWorkLst.push([work.id, work.ac_web]);
            }
            this.setState({workLst: newWorkLst});
        }
        getWorks();
    }
    async reloadEntry(id){
        let newItem = await arachne.edition.get({id: id}); newItem = newItem[0];
        let currentElements = this.state.currentElements;
        const indexOfNewItem = currentElements.findIndex(i => i.id===newItem.id)
        currentElements[indexOfNewItem] = newItem;
        this.setState({currentElements: currentElements, item: newItem});
    }
    async searchQuery(newQuery, order){
        this.props.status("searching");
        const count = await arachne.edition.search(newQuery, {count:true, order:order});
        const currentElements = await arachne.edition.search(newQuery, {limit:50, order:order});
        console.log(count);
        if(count[0]["count"]>1){this.props.status("found", `${count[0]["count"]} Einträge gefunden.`)}
        else if(count[0]["count"]===1){this.props.status("found", "1 Eintrag gefunden.")}
        else{this.props.status("notFound")}
        this.setState({
            query: newQuery,
            queryOrder: order,
            count: count[0]["count"],
            maxPage: Math.floor(count[0]["count"]/50)+1,
            currentPage: 1,
            currentElements: currentElements,
            selectionDetail: {ids:[]}
        });
    }
    async loadPage(newPage){
        const currentElements = await arachne.edition.search(this.state.query, {limit:50, offset:((newPage-1)*50), order:this.state.queryOrder});
            this.setState({
                currentPage: newPage,
                currentElements: currentElements,
                selectionDetail: {ids:[]}
            });
    }
}
class RessourceBox extends React.Component{
    constructor(props){
        super(props);
        this.state = {selection: {currentId: null, ids:[]}}
    }
    render(){
        if(this.props.count>0){
            const resTypes = {
                0: "Edition (relevant)",
                1: "Edition (veraltet)",
                2: "Handschrift",
                3: "Alter Druck (relevant)",
                4: "Alter Druck (veraltet)",
                5: "Sonstiges"
            };
    
            let cEls = [];
            for(const cEl of this.props.currentElements){
                cEls.push(<tr key={cEl.id} id={cEl.id} onDoubleClick={e=>{this.props.showDetail(this.props.currentElements.find(i =>i.id === parseInt(e.target.closest("tr").id)))}}><td title={"ID: "+cEl.id}>{cEl.label}</td><td>{resTypes[cEl.ressource]}</td><td dangerouslySetInnerHTML={parseHTML(cEl.opus)}></td><td><a>öffnen</a>{
                    cEl.url?<FontAwesomeIcon style={{fontSize:"14px", marginLeft: "10px"}} icon={faExternalLinkAlt} />:null
                }</td></tr>);
            }

            return (
            <div style={{gridArea: this.props.gridArea, padding:"0 10px"}}>
                <Navigator loadPage={newPage=>{this.props.loadPage(newPage)}} currentPage={this.props.currentPage} maxPage={this.props.maxPage} />
                <table width="100%">
                    <thead style={{textAlign:"left"}}><tr><th>Kürzel</th><th>Typ</th><th>vrkn. Werk</th><th>Link</th></tr></thead>
                    <tbody>{cEls}</tbody>
                </table>
            </div>);
        } else {
            return null;
        }
    }
}

class RessourceAside extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            id: this.props.item.id,
            ressource: this.props.item.ressource,
            work_id: this.props.item.work_id,
            editor: this.props.item.editor,
            year: this.props.item.year,
            volume: this.props.item.volume,
            volumeContent: this.props.item.volumeContent,
            serie: this.props.item.serie,
            location: this.props.item.location,
            library: this.props.item.library,
            signature: this.props.item.signature
        };
    }
    render(){
        return (
<div style={{
            position: "fixed",
            top: 0,
            bottom: 0,
            right: 0,
            width: "400px",
            padding: "10px 15px",
            boxShadow: "rgb(60, 110, 113) 0px 0px 2px"
        }} className="mainColors">
    <div style={{
        display: "grid",
        gridTemplateColumns: "120px auto",
        gridTemplateRows: "1fr 5px 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr",
        rowGap: "10px",
        margin: "35px 0 30px 0"
    }}>
        <div className="minorTxt" style={{gridArea: "1/1/1/3", textAlign: "right"}}>
            <i>ID {this.state.id}</i>
        </div>

        <div style={{gridArea: "3/1/3/2"}}>Werk:</div>
        <div style={{gridArea: "3/2/3/3"}}><SelectMenu options={this.props.workLst} value={this.state.work_id} onChange={e=>{this.setState({work_id: e.target.value})}} /></div>
        <div style={{gridArea: "4/1/4/2"}}>Ressource:</div>
        <div style={{gridArea: "4/2/4/3"}}><SelectMenu options={[[0, "Edition (relevant)"], [1, "Edition (veraltet)"], [2, "Handschrift"], [3, "Alter Druck (relevant)"], [4, "Alter Druck (veraltet)"], [5, "Sonstiges"]]} value={this.state.ressource} onChange={e=>{this.setState({ressource: parseInt(e.target.value)})}} /></div>
        {this.state.ressource===0||this.state.ressource===1||this.state.ressource===5?[
            <div key="0" style={{gridArea: "5/1/5/2"}}>Editor:</div>,
            <div key="1" style={{gridArea: "5/2/5/3"}}><input type="text" style={{width: "96%"}} value={this.state.editor} onChange={e=>{this.setState({editor: e.target.value})}} /></div>,
            <div key="2" style={{gridArea: "6/1/6/2"}}>Jahr:</div>,
            <div key="3" style={{gridArea: "6/2/6/3"}}><input type="text" style={{width: "96%"}} value={this.state.year} onChange={e=>{this.setState({year: e.target.value})}} /></div>,
            <div key="4" style={{gridArea: "7/1/7/2"}}>Band:</div>,
            <div key="5" style={{gridArea: "7/2/7/3"}}><input type="text" style={{width: "96%"}} value={this.state.volume} onChange={e=>{this.setState({volume: e.target.value})}} /></div>,
            <div key="6" style={{gridArea: "8/1/8/2"}}>Bandinhalt:</div>,
            <div key="7" style={{gridArea: "8/2/8/3"}}><input type="text" style={{width: "96%"}} value={this.state.volumeContent} onChange={e=>{this.setState({volumeContent: e.target.value})}} /></div>,
            <div key="8" style={{gridArea: "9/1/9/2"}}>Reihe:</div>,
            <div key="9" style={{gridArea: "9/2/9/3"}}><SelectMenu options={[[0, ""], [1, "Migne PL"], [2, "ASBen."], [3, "ASBoll."], [4, "AnalBoll."], [5, "Mon. Boica"], [6, "Ma. Schatzverzeichnisse"], [7, "Ma. Bibliothekskataloge"]]} value={this.state.serie} onChange={e=>{this.setState({serie: parseInt(e.target.value)})}} /></div>
        ]:null}
        {this.state.ressource===2?[
            <div key="0" style={{gridArea: "5/1/5/2"}}>Stadt:</div>,
            <div key="1" style={{gridArea: "5/2/5/3"}}><input type="text" style={{width: "96%"}} value={this.state.location} onChange={e=>{this.setState({location: e.target.value})}} /></div>,
            <div key="2" style={{gridArea: "6/1/6/2"}}>Bibliothek:</div>,
            <div key="3" style={{gridArea: "6/2/6/3"}}><input type="text" style={{width: "96%"}} value={this.state.library} onChange={e=>{this.setState({library: e.target.value})}} /></div>,
            <div key="4" style={{gridArea: "7/1/7/2"}}>Signatur:</div>,
            <div key="5" style={{gridArea: "7/2/7/3"}}><input type="text" style={{width: "96%"}} value={this.state.signature} onChange={e=>{this.setState({signature: e.target.value})}} /></div>
        ]:null}
        {this.state.ressource===3||this.state.ressource===4?[
            <div key="0" style={{gridArea: "5/1/5/2"}}>Drucker:</div>,
            <div key="1" style={{gridArea: "5/2/5/3"}}><input type="text" style={{width: "96%"}} value={this.state.editor} onChange={e=>{this.setState({editor: e.target.value})}} /></div>,
            <div key="2" style={{gridArea: "6/1/6/2"}}>Ort:</div>,
            <div key="3" style={{gridArea: "6/2/6/3"}}><input type="text" style={{width: "96%"}} value={this.state.location} onChange={e=>{this.setState({location: e.target.value})}} /></div>,
            <div key="4" style={{gridArea: "7/1/7/2"}}>Jahr:</div>,
            <div key="5" style={{gridArea: "7/2/7/3"}}><input type="text" style={{width: "96%"}} value={this.state.year} onChange={e=>{this.setState({year: e.target.value})}} /></div>,
        ]:null}
        <div style={{gridArea: "11/2/11/3"}}><input type="button" value="speichern" onClick={async ()=>{
            if((this.state.type===0||this.state.type===1||this.state.type===5)&&(!this.state.editor||!this.state.year)){
                this.props.status("error", "Geben Sie den Editor und das Jahr ein.");
            } else {
                this.props.status("saving", "Die Dateien werden hochgeladen.");
                // create new edition
                await arachne.edition.save({
                    id: this.state.id,
                    work_id: this.state.work,
                    ressource: this.state.type,
                    editor: this.state.editor,
                    year: this.state.year,
                    volume: this.state.volume,
                    vol_cont: this.state.volumeContent,
                    serie: this.state.serie,
                    /*comment,*/
                    location: this.state.location,
                    library: this.state.library,
                    signature: this.state.signature
                });
                this.props.status("saved", "Das Speichern war erfolgreich.");
                this.props.onUpdate(this.state.id);
            }
        }} /></div>
    </div>
</div>
        );
    }
    componentDidUpdate(){
        if(this.state.id!==this.props.item.id){
            this.setState({
                id: this.props.item.id,
                ressource: this.props.item.ressource,
                work_id: this.props.item.work_id,
                editor: this.props.item.editor,
                year: this.props.item.year,
                volume: this.props.item.volume,
                volumeContent: this.props.item.volumeContent,
                serie: this.props.item.serie,
                location: this.props.item.location,
                library: this.props.item.library,
                signature: this.props.item.signature
            });
        }
    }
}

export { Ressource };