import React from "react";
import { faExternalLinkAlt, faForward, faBackward } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { arachne } from "./arachne.js";
import { Navigator, parseHTML } from "./elements.js";

class OperaAside extends React.Component{
    render(){
        return <div>Seite</div>;
    }
}
class OperaBox extends React.Component{
    constructor(props){
        super(props);
        this.state = {oLst: []};
    }
    render(){
        let trLst = [];
        let i = 0;
        const cHitId = (this.state.hits&&this.state.hits.length>0)?this.state.hits[this.state.hitIndex].id:0;
        this.scrollRef = null;
        for(const o of this.state.oLst){
            let editionLst = [];
            if(o.editions_id){
                const editionsId = JSON.parse(o.editions_id);
                const editionsURL = JSON.parse(o.editions_url);
                const editionsLabel = JSON.parse(o.editions_label);
                for(let iE = 0; iE < editionsId.length; iE++){
                    let cURL = "/site/viewer/"+editionsId[iE];
                    let arrow = "";
                    if(editionsURL&&editionsURL[iE]!=""){
                        cURL=editionsURL[iE];
                        arrow = <span> <FontAwesomeIcon style={{fontSize:"14px"}} icon={faExternalLinkAlt} /></span>
                    }
                    let cLabel = "FEHLER!";
                    if(editionsLabel){cLabel=editionsLabel[iE]}
                    editionLst.push(<li key={iE}><a href={cURL} target="_blank" rel="noreferrer">{cLabel}{arrow}</a></li>);
                }
            }
            i++;
            let hitStyle = {};
            if(o.id===cHitId){hitStyle = "searchHit"; this.scrollRef = React.createRef()}
            else if(cHitId>0&&this.state.hits.some(i=>i.id===o.id)){hitStyle = "searchAllHits"}
            if(this.state.listName=="opera_maiora"){
                let abbr = `<aut>${o.abbr}</aut>`;
                let full = o.full;
                if(o.work_id>0&&o.author_id===null){
                    abbr= `<span>&nbsp;&nbsp;&nbsp;${o.abbr}</span>`;
                    full = `<span>&nbsp;&nbsp;&nbsp;${o.full}</span>`;
                }
                trLst.push(<tr className={hitStyle} ref={o.id===cHitId?this.scrollRef:null} key={i}><td className="c1" dangerouslySetInnerHTML={parseHTML(o.date_display)}></td><td className="c2" dangerouslySetInnerHTML={parseHTML(abbr)}></td><td className="c3" dangerouslySetInnerHTML={parseHTML(full)}></td><td className="c4">{o.bibliography}<ul className="noneLst">{editionLst}</ul></td><td className="c5" dangerouslySetInnerHTML={parseHTML(o.comment)}></td></tr>);
            } else if(this.state.listName==="opera_minora"){
                trLst.push(<tr className={hitStyle} ref={o.id===cHitId?this.scrollRef:null} key={i}><td className="c1_min" dangerouslySetInnerHTML={parseHTML(o.date_display)}></td><td className="c2_min" dangerouslySetInnerHTML={parseHTML(o.citation)}></td><td className="c5_min"><span dangerouslySetInnerHTML={parseHTML(o.bibliography)}></span><ul className="noneLst">{editionLst}</ul></td></tr>);
            }
        }
        return <div style={{gridArea: this.props.gridArea}}>
                <Navigator currentPage={this.state.currentPage} maxPage={this.state.oMax} loadPage={nextPage=>{this.loadPage(nextPage)}} />
                <div className="operaBox">
                <table><tbody>{trLst}</tbody></table>
                </div>
                <div className="SearchBar mainColors"><input type="text" style={{boxShadow: "none", padding: "0px"}} placeholder="Suche..." onKeyUp={async e=>{
                    if(e.keyCode === 13){
                        if(e.target.value!=""&&this.state.cSearch!=e.target.value){
                            const hits = await arachne[this.props.listName].search([{c: "search", o: "=", v: `*${e.target.value}*`}], {select: ["id"]});
                            if(hits.length>0){
                                this.setState({hitIndex: 0, cSearch: e.target.value, maxHits: hits.length, hits: hits});
                                this.loadPage(Math.floor(hits[0].id/30)+1);
                            } else {
                                this.setState({cSearch: e.target.value, maxHits: 0, hits: []});
                            }
                        }
                    }
                }} />
                {this.state.maxHits>0&&<div style={{display:"inline", marginLeft: "20px"}}>
                    <FontAwesomeIcon icon={faBackward} style={{fontSize: "14px"}} onClick={()=>{this.gotoSearchResult(-1)}} /> <span>{this.state.hitIndex+1}</span>/<span>{this.state.maxHits}</span> <FontAwesomeIcon icon={faForward} style={{fontSize: "14px"}} onClick={()=>{this.gotoSearchResult(1)}} />
                </div>}
                </div>
            </div>;
    }
    gotoSearchResult(move){
        if(move===1&&this.state.hitIndex<this.state.maxHits-1){
            this.loadPage(Math.floor(this.state.hits[this.state.hitIndex+1].id/30)+1);
            this.setState({hitIndex: this.state.hitIndex+1});
        } else if (move===1){
            this.loadPage(1);
            this.setState({hitIndex: 0});
        } else if (move===-1&&this.state.hitIndex>0){
            this.loadPage(Math.floor(this.state.hits[this.state.hitIndex-1].id/30)+1);
            this.setState({hitIndex: this.state.hitIndex-1});
        } else if (move===-1){
            this.loadPage(Math.floor(this.state.hits[this.state.maxHits-1].id/30)+1);
            this.setState({hitIndex: this.state.maxHits-1});
        }
    }
    async loadPage(newPage){
        const oLst = await arachne[this.props.listName].getAll({limit: 30, offset: (newPage-1)*30});
        this.setState({oLst: oLst, currentPage: newPage});   
    }
    componentDidMount(){
        const getLst = async () =>{
            const oMax = await arachne[this.props.listName].getAll({count: true});
            const oLst = await arachne[this.props.listName].getAll({limit: 30, offset: 0});
            this.setState({listName: this.props.listName, oLst: oLst, oMax: Math.floor(oMax[0]["count"]/30)+1, currentPage: 1});
        };
        getLst();
    }
    componentDidUpdate(prevProps){
        if(this.props.listName!=prevProps.listName){
            const getLst = async () =>{
                const oMax = await arachne[this.props.listName].getAll({count: true});
                const oLst = await arachne[this.props.listName].getAll({limit: 30, offset: 0});
                this.setState({oLst: oLst, listName: this.props.listName, oMax: Math.floor(oMax[0]["count"]/30)+1, currentPage: 1});
            };
            getLst();
        }
        if(this.scrollRef){this.scrollRef.current.scrollIntoView({behavior: "smooth", block: "center"})}
    }
}
class Opera extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            selectionDetail: null
        }
    }
    render(){
        return (
            <div style={{padding: "0 10px", display: "grid", gridTemplateColumns: "auto 420px", gridTemplateRows: "auto", rowGap: "15px"}}>
                <OperaBox
                    listName={this.props.listName}
                    loadPage={move => {this.loadPage(move)}}
                    currentElements={this.state.currentElements}
                    count={this.state.count}
                    currentPage={this.state.currentPage}
                    pageMax={this.state.pageMax}
                    gridArea={(this.state.selectionDetail)?"2/1/2/2":"2/1/2/3"}
                    /*showDetail={item => {
                        this.setState({selectionDetail: item.selection, itemDetail: item.item});
                    }}*/
                />
                {this.state.selectionDetail?<OperaAside status={this.props.status} selection={this.state.selectionDetail} item={this.state.itemDetail} onUpdate={ids=>{this.reloadZettel(ids)}} />:""}
            </div>
            );
    }
}

export { Opera };